body {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
}
html {
  padding: 0;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
}
.button {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  color: #444444;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.svg-library {
  display: none;
}
.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-top: 1px solid #ececec;
}
.main-navigation__link {
  display: flex;
  justify-content: center;
  padding: 27px 0;
  text-decoration: none;
  border-bottom: 1px solid #ececec;
}
.main-navigation__link-text {
  box-sizing: border-box;
  min-width: 250px;
  padding-left: 40px;
  text-align: left;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
}
.main-navigation__link:focus .main-navigation__link-text,
.main-navigation__link:hover .main-navigation__link-text {
  color: rgba(0, 0, 0, 0.6);
}
.main-navigation__link:active .main-navigation__link-text {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
    border: none;
  }
  .main-navigation__item {
    margin-right: 18px;
  }
  .main-navigation__link {
    padding: 10px 0;
    border-bottom: none;
  }
  .main-navigation__link-text {
    min-width: 150px;
    padding-left: 0;
    font-size: 15px;
    line-height: 24px;
  }
}
